import React, { useEffect, useState } from 'react'
import './App.css';
import axios from "axios";
function App() {
  const [state, nextstate] = useState([]);
  const [abc, abc1] = useState([]);
  const [abc4, abc5] = useState([]);
  const [tog, tog1] = useState(true)
  const [t, g] = useState("paid");
  const [loading, setloading] = useState(false);
  const [e1, e2] = useState(true)

  const inputEvent = (event) => {

    const x = event.target.value;
    nextstate(x);
  }


  useEffect(() => {

    getData();

  }, []);

  async function getData() {
    try {
      await axios.get(`https://o136z8hk40.execute-api.us-east-1.amazonaws.com/dev/get-list-of-conferences/`)
        .then(res => {
          abc1(res.data.free)
          abc5(res.data.paid)
        });
      setloading(true);
    }

    catch (e) {
      e2(!e1)
    }
  }
  return (
    <>

      <div className="heading">
        <h1>Welcome</h1>
        <h3>&mdash; Conference &mdash;</h3>
      </div>
      <div>
        <span style={{
          fontSize: "20px",
          padding: "10px 20px"
        }}>Search by City or Title : </span> <input className="sbox" type="text" placeholder="Hyderbad" value={state} onChange={inputEvent} />
        <button classname="toggle" onClick={() => { tog1(!tog); if (t === "paid") g("free"); else g("paid") }}>Click for {t} service</button>
      </div>

      {e1 === false ? <div className="broken">Invalid 404 error</div> : tog ? <div>
         
        <div className="menu">
          <h1 className="tit">Free Courses</h1>
          {abc.filter((val) => {
            const { confName, city } = val;
            if (state === "") {
              return confName
            }
            else if (confName.toString().toLowerCase().includes(state) || (city.toString().toLowerCase().includes(state.toString().toLowerCase()))) {
              return confName
            }
          }

          ).map((a) => {
            const { confName, confEndDate, confStartDate, imageURL, entryType, confUrl, city } = a;
            let cc = imageURL.replace(/['"]+/g, '');
            return (
              <div className="container">
                <article className="xyz">
                  <article>
                    <h2>Conference Name : <span>{confName}</span></h2>
                    <div className="Service">
                      <h2 style={{ color: "crimson" }}>{entryType} service</h2>
                    </div>
                    <div className="text">
                      <div>

                        <img src={cc} alt="noImage" />
                      </div>
                      <div className="city">
                        <p>{city ? <span>{city}</span> : <span>...Randam city ...</span>}</p>
                      </div>
                      <a href={confUrl}>Register Now</a>
                      <div className="iline">
                        <div style={{ float: 'left' }}>
                          <p>Start Date : <span style={{ fontSize: "20px" }}>{confStartDate}</span></p>
                        </div>
                        <div style={{ float: 'right' }}>
                          <p>End Date : <span style={{ fontSize: "20px" }}>{confEndDate}</span></p>
                        </div>
                      </div>
                    </div>
                  </article>
                </article>
              </div>

            )
          })

          }
        </div>
      </div> : <div>

        <div className="menu">
          <h1 className="tit" >Paid Courses</h1>
          {abc4.filter((val) => {
            const { confName, city } = val;
            if (state === "") {
              return confName
            }
            else if (confName.toString().toLowerCase().includes(state) || (city.toString().toLowerCase().includes(state.toString().toLowerCase()))) {
              return confName
            }


          }

          ).map((a) => {
            const { confName, confEndDate, confStartDate, imageURL, entryType, confUrl, city } = a;
            let aa = imageURL.replace(/['"]+/g, '');

            return (

              <div className="container">

                <article className="xyz">
                  <article>
                    <h2>Confrence Name : <span>{confName}</span></h2>
                    <div className="Service">
                      <h2 style={{ color: "crimson" }}>{entryType} service</h2>
                    </div>
                    <div className="text">
                      <div>
                        <img src={aa} alt="noImage" />
                      </div>
                      <div className="city">
                        <p>{city ? <span>{city}</span> : <span>...Randam city ...</span>}</p>
                      </div>
                      <a href={confUrl}>Register Now</a>
                      <div className="iline">
                        <div style={{ float: 'left' }}>
                          <p>Start Date : <span style={{ fontSize: "20px" }}>{confStartDate}</span></p>
                        </div>
                        <div style={{ float: 'right' }}>
                          <p>End Date : <span style={{ fontSize: "20px" }}>{confEndDate}</span></p>
                        </div>
                      </div>
                    </div>
                  </article>
                </article>
              </div>

            )
          })

          }
        </div>
      </div>}






    </>
  );
}

export default App;
